.wrapper {
  -webkit-perspective: 1000px;
          perspective: 1000px;
}
.wrapper:hover .cube-front {
  -webkit-transform: translateZ(100px);
          transform: translateZ(100px);
}
.wrapper:hover .cube-back {
  -webkit-transform: translateZ(-100px) rotateY(180deg);
          transform: translateZ(-100px) rotateY(180deg);
}
.wrapper:hover .cube-left {
  -webkit-transform: translateX(-100px) rotateY(-90deg);
          transform: translateX(-100px) rotateY(-90deg);
}
.wrapper:hover .cube-right {
  -webkit-transform: translateX(100px) rotateY(90deg);
          transform: translateX(100px) rotateY(90deg);
}
.wrapper:hover .cube-top {
  -webkit-transform: translateY(-100px) rotateX(90deg);
          transform: translateY(-100px) rotateX(90deg);
}
.wrapper:hover .cube-bottom {
  -webkit-transform: translateY(100px) rotateX(-90deg);
          transform: translateY(100px) rotateX(-90deg);
}
.wrapper .cube {
  width: 80px;
  height: 80px;
  -webkit-animation: rotate 10s infinite linear;
          animation: rotate 10s infinite linear;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
}
.wrapper .cube div {
  width: 100%;
  height: 100%;
  border: 1px solid #fff;
  position: absolute;
  -webkit-transition: -webkit-transform 0.4s ease-in;
  transition: -webkit-transform 0.4s ease-in;
  -o-transition: transform 0.4s ease-in;
  transition: transform 0.4s ease-in;
  transition: transform 0.4s ease-in, -webkit-transform 0.4s ease-in;
  background: url('../image/32317467.jpg') no-repeat center;
  background-size: 100% 100%;
  -webkit-box-shadow: 0px 0px 10px #fff;
          box-shadow: 0px 0px 10px #fff;
}
.wrapper .cube .text {
  width: 98px;
  height: 98px;
}
.wrapper .cube-front {
  -webkit-transform: translateZ(40px);
          transform: translateZ(40px);
}
.wrapper .cube-back {
  -webkit-transform: translateZ(-40px) rotateY(180deg);
          transform: translateZ(-40px) rotateY(180deg);
}
.wrapper .cube-left {
  -webkit-transform: translateX(-40px) rotateY(-90deg);
          transform: translateX(-40px) rotateY(-90deg);
}
.wrapper .cube-right {
  -webkit-transform: translateX(40px) rotateY(90deg);
          transform: translateX(40px) rotateY(90deg);
}
.wrapper .cube-top {
  -webkit-transform: translateY(-40px) rotateX(90deg);
          transform: translateY(-40px) rotateX(90deg);
}
.wrapper .cube-bottom {
  -webkit-transform: translateY(40px) rotateX(-90deg);
          transform: translateY(40px) rotateX(-90deg);
}
@-webkit-keyframes rotate {
  from {
    -webkit-transform: rotateX(0deg) rotateY(0deg) rotateZ(0deg);
            transform: rotateX(0deg) rotateY(0deg) rotateZ(0deg);
  }
  to {
    -webkit-transform: rotateX(360deg) rotateY(360deg) rotateZ(360deg);
            transform: rotateX(360deg) rotateY(360deg) rotateZ(360deg);
  }
}
@keyframes rotate {
  from {
    -webkit-transform: rotateX(0deg) rotateY(0deg) rotateZ(0deg);
            transform: rotateX(0deg) rotateY(0deg) rotateZ(0deg);
  }
  to {
    -webkit-transform: rotateX(360deg) rotateY(360deg) rotateZ(360deg);
            transform: rotateX(360deg) rotateY(360deg) rotateZ(360deg);
  }
}



