/**
black theme
 */
html,
body,
header,
section,
div,
address,
p {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: 0;
  padding: 0;
}
html {
  font-size: 14px;
  min-height: 100%;
}
body {
  font-size: 1rem;
  font-family: 'microsoft yahei', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  background: #2b2c2c;
  color: #979899;
}
h2 {
  font-size: 4rem;
  font-weight: 200;
  margin: 0 0 1rem;
}
.img-responsive {
  max-width: 100%;
  height: auto;
  display: block;
}
.text-center {
  text-align: center;
}
.text-light {
  color: #e0a80d;
}
.hide {
  display: none;
}
.progress {
  position: relative;
  background: #404242;
  height: 5px;
}
.progress .progress-bar {
  height: 100%;
  background: #e0a80d;
}
/* loading */
body {
  background: #2b2c2c;
}
.loading {
  position: fixed;
  top: 45%;
  left: 50%;
}
.container {
  max-width: 1200px;
  margin: 0 auto;
}
.container .row {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row wrap;
      flex-flow: row wrap;
}
@media (min-width: 768px) {
  .col-md-5 {
    width: 41.66%;
  }
  .col-md-6 {
    width: 50%;
  }
  .col-md-7 {
    width: 58.33%;
  }
}
@media (min-width: 1200px) {
  .col-lg-3 {
    width: 25%;
  }
  .col-lg-4 {
    width: 33.33%;
  }
}
@media (max-width: 768px) {
  [class*="col-lg"],
  [class*="col-md"] {
    width: 100%;
  }
}
ul {
  padding-left: 15px;
  margin-top: 0;
}
ul.inline {
  list-style: none;
  margin-left: -0.5rem;
  padding-left: 0;
}
ul.inline li {
  float: left;
  display: inline-block;
  padding: 0 0.5rem;
}
.useful-link {
  position: fixed;
  top: 20px;
  right: 20px;
  z-index: 5;
}
.useful-link img {
  width: 14px;
  vertical-align: middle;
  margin-right: 5px;
}
.useful-link a {
  float: left;
  margin-left: 20px;
}
.useful-link .github-button {
  font-size: 14px;
  border: 1px solid #d5d5d5;
  border-radius: 3px;
  padding: 3px 9px;
  color: #333;
  line-height: 14px;
  text-shadow: 0 1px 0 rgba(255, 255, 255, 0.9);
  background-color: #eee;
  background-image: -webkit-linear-gradient(top, #fcfcfc, #eee);
  background-image: -webkit-gradient(linear, left top, left bottom, from(#fcfcfc), to(#eee));
  background-image: -o-linear-gradient(top, #fcfcfc, #eee);
  background-image: linear-gradient(to bottom, #fcfcfc, #eee);
  -ms-filter: "progid:DXImageTransform.Microsoft.gradient(startColorstr='#fcfcfc', endColorstr='#eeeeee')";
  display: block;
  text-decoration: none;
}
.useful-link .github-button:hover {
  background-color: #ddd;
  background-image: -webkit-linear-gradient(top, #eee, #ddd);
  background-image: -webkit-gradient(linear, left top, left bottom, from(#eee), to(#ddd));
  background-image: -o-linear-gradient(top, #eee, #ddd);
  background-image: linear-gradient(to bottom, #eee, #ddd);
  border-color: #ccc;
  -ms-filter: "progid:DXImageTransform.Microsoft.gradient(startColorstr='#eeeeee', endColorstr='#dddddd')";
}
.useful-link .github-button span {
  vertical-align: middle;
}
.useful-link .theme-link {
  width: 24px;
  height: 24px;
  background: #757575;
  border-radius: 3px;
}
.section-bg {
  position: absolute;
  width: 50%;
  height: 100%;
  z-index: 1;
}
.section-header-bg {
  background: #2b2c2c;
  left: 0;
}
.section-content-bg {
  background: #292a2a;
  left: 50%;
}
.section {
  position: relative;
}
.section > .container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row wrap;
      flex-flow: row wrap;
}
.section a {
  color: #979899;
  text-decoration: none;
  display: inline-block;
}
.section a:hover {
  text-decoration: none;
}
.section .header {
  width: 30%;
  padding: 4rem 5rem;
  z-index: 2;
  background: #2b2c2c;
  text-align: right;
}
.section .header .header-box {
  position: relative;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-direction: column;
      flex-direction: column;
}
.section .header .title {
  font-size: 1.5rem;
}
.section .header .title small {
  font-size: 1rem;
}
.section .section-content {
  width: 70%;
  padding: 4rem 3rem 4rem 5rem;
  background: #292a2a;
  z-index: 2;
}
.section .section-content .content-box {
  position: relative;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-direction: column;
      flex-direction: column;
}
.section .section-content .intro p {
  margin-bottom: 1rem;
  line-height: 2rem;
}
.section-header .header {
  height: 100vh;
}
.section-header .section-content {
  height: 100vh;
}
.section-header .avatar {
  max-width: 200px;
  margin: 0 auto;
}
.section-header .name {
  font-size: 2rem;
  font-weight: 200;
  margin-top: 1rem;
  margin-bottom: 4rem;
}
.section-header .name-slogan {
  position: relative;
  top: -2rem;
}
.section-header .description {
  font-size: 1.2rem;
  margin-bottom: 2rem;
}
.section-header .contact-info {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
}
.section-header .contact-info > div:first-child {
  border-bottom: 2px dashed #404242;
  padding-bottom: 1rem;
}
.section-header .contact-info h4 {
  font-size: 1rem;
  margin: 1.5rem 0;
}
.section-header .contact-info .info {
  line-height: 1rem;
}
.section-header address {
  font-style: normal;
  line-height: 1rem;
}
.experience {
  margin-left: 2px;
  margin-top: 1rem;
  padding-left: 24px;
  border-left: 2px solid #404242;
}
.experience .item {
  position: relative;
  line-height: 2rem;
  margin-top: -15px;
  margin-bottom: 3rem;
}
.experience .item:before {
  content: '';
  position: absolute;
  top: 6px;
  left: -30px;
  background: #e0a80d;
  border-radius: 5px;
  width: 10px;
  height: 10px;
  -webkit-box-shadow: 0 0 0 6px #404242;
          box-shadow: 0 0 0 6px #404242;
}
.experience .item .time {
  font-size: 0.8rem;
  color: #757575;
  line-height: 1.6rem;
}
.experience .item .title {
  font-size: 1.2rem;
}
.experience .item .proTitle {
  font-size: 1rem;
}
.experience .item .description {
  color: #757575;
}
.skill .item {
  margin-bottom: 2rem;
  padding-right: 2rem;
}
.skill .text-info {
  margin-bottom: 1rem;
  font-size: 1.2rem;
}
.skill .num {
  display: inline-block;
  margin-right: 1rem;
}
.section:nth-child(even) .header {
  background: #292a2a;
}
.section:nth-child(even) .section-header-bg {
  background: #292a2a;
}
.section:nth-child(even) .section-content {
  background: #2b2c2c;
}
.section:nth-child(even) .section-content-bg {
  background: #2b2c2c;
}
.usually .item {
  margin-right: 3rem;
  margin-bottom: 1rem;
  line-height: 2rem;
  display: inline-block;
}
.usually .item h3 {
  margin-top: 0.5rem;
  margin-bottom: 0.8rem;
  line-height: 1rem;
}
.contact {
  margin-bottom: 2rem;
}
.contact h4 {
  margin-top: 0.3rem;
  margin-bottom: 0.8rem;
}
@media (max-width: 1120px) {
  .section .header {
    width: 100%;
    text-align: left;
  }
  .section .section-content {
    width: 100%;
  }
  .section-header .header {
    height: auto;
  }
  .section-header .section-content {
    height: auto;
  }
  .section-header .contact-info {
    position: relative;
  }
  .section:nth-child(even) .header {
    background: #2b2c2c;
  }
  .section:nth-child(even) .section-header-bg {
    background: #2b2c2c;
  }
  .section:nth-child(even) .section-content {
    background: #292a2a;
  }
  .section:nth-child(even) .section-content-bg {
    background: #292a2a;
  }
}
@media (max-width: 768px) {
  .section .header {
    padding: 15px;
  }
  .section .section-content {
    padding: 15px;
  }
  .section-header .name-slogan {
    top: 0;
  }
  h2 {
    font-size: 2.5rem;
  }
}
/* print */
@media print {
  .col-md-6 {
    width: 50%;
  }
  .no-print {
    display: none;
    opacity: 0;
  }
  .show-print-block {
    display: block!important;
    opacity: 1!important;
  }
  .section .section-content {
    padding: 0 12px;
  }
  .section-header .description {
    text-align: center;
  }
  .section-header .contact-info > div:first-child {
    border-bottom: none;
    padding-bottom: 0;
  }
  .section-header .contact-info h4 {
    width: 80px;
    float: left;
    margin: 10px 0;
    line-height: 1rem;
  }
  .section-header .contact-info .info {
    padding: 10px 0 10px 80px;
    line-height: 1rem;
  }
  .section-header .contact-info a {
    display: block;
  }
  .section-header .name {
    margin-bottom: 1rem;
  }
  .skill .item {
    margin-bottom: 0.5rem;
  }
  .experience .item {
    margin-bottom: 1rem;
  }
}
/* animate */
.inShow {
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  -webkit-animation-name: inShow;
  animation-name: inShow;
}
@-webkit-keyframes inShow {
  from {
    -webkit-transform: translate3d(0, 100%, 0);
    transform: translateY(50px) scale(0.9);
    visibility: visible;
    opacity: 0;
  }
  to {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0) scale(1);
    opacity: 1;
  }
}
@keyframes inShow {
  from {
    -webkit-transform: translate3d(0, 100%, 0);
    transform: translateY(50px) scale(0.9);
    visibility: visible;
    opacity: 0;
  }
  to {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0) scale(1);
    opacity: 1;
  }
}
.progressShow {
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  -webkit-animation-name: progressShow;
  animation-name: progressShow;
}
@-webkit-keyframes progressShow {
  from {
    width: 0;
  }
}
@keyframes progressShow {
  from {
    width: 0;
  }
}



